
import axios from 'axios'
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Alert from 'vux/src/components/alert/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
components: {
XHeader,
Flexbox,
FlexboxItem,Cell,Group,XButton,XTable,Alert
},
data() {
return {
number1:1,
number2:1,
userId:cookie.get('userId'),
startTime1:'',
activeTitle:'',
activePace:'',
activeCreatePeopleName:'',
activeId:'',
activeType:false,
active_Context:'',
show:false,
activeComplete:'',
isActive:true,
signupstatus:'',
msg:'',
endTime1:'',
count:'0'
};
},
filters: {
formatDuring: function (value) {
if(value == "" || value == null || value == undefined){
var value="无"
return value

}else {
Date.prototype.toLocaleString = function(){
let months = this.getMonth()+1;
if(months < 10){
months = '0' + months;
}
let dates = this.getDate();
if(dates < 10){
dates = '0' + dates;
}
let hours = this.getHours();
if(hours < 10){
hours = '0' + hours;
}
let minutes = this.getMinutes();
if(minutes < 10){
minutes = '0' + minutes;
}
return this.getFullYear() +'.'+months+'.'+dates+' '+hours+':'+minutes
}
return new Date(value).toLocaleString();
}
},
formatDurings: function (value) {

Date.prototype.toLocaleString = function(){
let minutes = this.getMinutes();
if(minutes < 10){
minutes = '0' + minutes;
}
return this.getFullYear() +'.'+ (this.getMonth()+1)+'.'+this.getDate();
}
return new Date(value).toLocaleString();
return new Date(value).toLocaleString();

}
},
methods:{
getActivity(){
axios({
method: 'get',
url: 'active/getParticipateActive',
params: {
pageNum:1,
pageSize:1,
departmentid:this.$store.getters.user.departmentid,
userId:this.$store.getters.user.userid
}
}) .then((res)=> {
this.activeTitle=res.data.list[0].activeName;
this.startTime1=res.data.list[0].startTime;
this.endTime1=res.data.list[0].endTime;
this.activePace=res.data.list[0].activePace;
this.activeCreatePeopleName=res.data.list[0].activeCreatePeopleName;
this.active_Context=res.data.list[0].active_Context;
this.activeId=res.data.list[0].id;
this.signupstatus=res.data.list[0].signupstatus;
this.activeType = res.data.list[0].activeType === 5 ? true : false;
}).catch(function (error) {
console.log(error);
});


},
gettimes1(){
axios({
method: 'post',
url: 'active/getParticipateCount',
params: {
userId:this.userId,

year: new Date().getFullYear()
}
}) .then((res)=> {

this.number1=res.data
}) .catch(function (error) {
console.log(error);
});
} ,
gettimes2(){
axios({
method: 'post',
url: 'active/getParticipateCount',
params: {
userId:this.userId,
activeType:5,
year: new Date().getFullYear()
}
}) .then((res)=> {

this.number2=res.data


})
.catch(function (error) {
console.log(error);
});
},submit(){
axios({
method: 'post',
url: 'active/participate',
params: {
userId:this.userId,
activeId:this.activeId
}
}) .then((res)=> {

this.msg=res.msg;
this.show=true

})
.catch(function (error) {
console.log(error);
});

},
getAlreadyActive(){
axios({
method: 'get',
url: 'active/getEnjoyActiveByUserId',
params: {
userId:this.userId,
pageNum:1,
pageSize:4
}
}) .then((res)=>{
this.activeComplete=res.data;

}).catch(function (error) {
console.log(error);
});
},
getActiveCount(){
axios({
method: 'get',
url: 'active/getParticipateActiveCount',
params: {
departmentid:this.$store.getters.user.departmentid,
userId:this.$store.getters.user.userid
}
}) .then((res)=>{
console.log(res);
this.count=res.data;

}).catch(function (error) {
console.log(error);
});
},
onHide () {
window.location.reload()
},
onShow () {
console.log('on show')
},submit1(){
this.$vux.alert.show({title:'请勿重复报名'});


},

},
mounted() {
this.getActivity();
this.gettimes1();
this.gettimes2();
this.getAlreadyActive();
this.getActiveCount()
}
};



